.App-task {
  border: 2px solid rgb(11, 109, 148);
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
  padding: 1rem;
  position: relative;
}

.App-task.selected-favorite,
.App-task.selected-completed,
.App-task.selected-not-completed {
  border: 2px solid red;
}

.App-check-completed {
  margin-right: 1.5vw;
}

.App-task-edit {
  flex: 1;
}

.App-task-edit input {
  width: 90%;
  padding: 0.5rem;
  font-size: calc(1vw + 0.7em);
}

.icon-favorite,
.icon-delete {
  /* border: 2px solid red; */
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
}

.action-task {
  /* border: 1px solid blue; */
  margin-right: 0.7vw;
  cursor: pointer;
}

.icon-bin,
.icon-star-empty {
  display: inline-block;
  width: 100%;
  height: 100%;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}

.icon-star-empty.selected {
  fill: orange;
}

.style-completed {
  border: 1px solid rgb(43, 45, 61);
  position: absolute;
  width: 90%;
  margin-left: 1rem;
  opacity: 0;
}

.style-completed.line-completed {
  opacity: 1;
}

.date-task {
  position: absolute;
  top: -30px;
  right: 0;
  font-size: .8rem;
}